*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

img {
  display: block;
  max-width: 100%;
}

a {
  display: block;
}

.container {
  width: min(90%, 90rem);
  margin-inline: auto;
}

:root {
  --Soft-orange: hsl(35, 77%, 62%);
  --Soft-red: hsl(5, 85%, 63%);
  --Off-white: hsl(36, 100%, 99%);
  --Grayish-blue: hsl(233, 8%, 79%);
  --Dark-grayish-blue: hsl(236, 13%, 42%);
  --Very-dark-blue: hsl(240, 100%, 5%);
  --font-size-paragraph: 0.9375rem;
  --font-size-title: 2.375rem;
  --font-size-side-title: 1.75rem;
  --font-size-counter: 1.875rem;
  --font-family: "Inter", sans-serif;
  --font-weight-heading: 900;
  --font-weight-subtitle: 700;
  --font-weight-paragraph: 400;
}

body {
  font-size: var(--font-size-paragraph);
  font-family: var(--font-family);
  line-height: 1.5;
}

p {
  font-weight: var(--font-weight-paragraph);
  color: var(--Dark-grayish-blue);
}

h1,
h2,
h3,
h4 {
  margin-bottom: 0.75em;
  line-height: 1.1;
  font-weight: var(--font-weight-subtitle);
}

h1 {
  font-weight: var(--font-weight-heading);
  font-size: var(--font-size-title);
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

.new__btn {
  color: var(--Off-white);
  background-color: var(--Soft-red);
  padding: 0.75em 1.5em;
  width: fit-content;
  margin-block: 1.5em 3em;
  letter-spacing: 2px;
  font-weight: 600;
  transition: all 0.4s ease-in-out;
}

.new__btn:hover {
  background-color: var(--Very-dark-blue);
}

.sidebar__title {
  color: var(--Soft-orange);
  font-size: var(--font-size-side-title);
}
.sidebar__subtitle {
  cursor: pointer;
  color: var(--Off-white);
  transition: all 0.3s ease-in-out;
}
.sidebar__subtitle:hover {
  color: var(--Soft-orange);
}

.card__subtitle {
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.card__subtitle:hover {
  color: var(--Soft-red);
}

.navbar {
  padding-block: 2em;
}
.navbar__flex {
  display: flex;
  gap: 1em;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 40em) {
  .navbar__flex {
    flex-direction: row;
    justify-content: space-between;
  }
}
.navbar__link {
  transition: color 0.2s ease-in-out;
}
.navbar__link:hover {
  color: var(--Soft-orange);
}
.navbar__list {
  display: flex;
  gap: 1em;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 40em) {
  .navbar__list {
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
}

@media (min-width: 45em) {
  .new--grid {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
  }
}
@media (min-width: 60em) {
  .new--grid {
    grid-template-columns: auto;
  }
}

@media (min-width: 60em) {
  .row {
    display: flex;
    gap: 2rem;
    justify-content: space-between;
  }
}
.sidebar {
  background-color: var(--Very-dark-blue);
  padding: 1.125em;
  margin-bottom: 4em;
}
.sidebar__line {
  border: 1px solid var(--Dark-grayish-blue);
}

.sidebar > * + * {
  margin-top: 2em;
}

@media (min-width: 60em) {
  .page-layout {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
}
.flex {
  display: flex;
  gap: 1em;
}

.card__img {
  max-width: 6.25rem;
}

.counter {
  font-size: var(--font-size-counter);
  font-weight: 600;
  color: var(--Grayish-blue);
}

.card:nth-of-type(3) {
  margin-bottom: 4rem;
}

.grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(16rem, 1fr));
}